import React from "react";
export default  class InputAlert extends React.Component{
    constructor(){
        super()
        this.state={
            value:'',
            top:-200,
        }
        this.downslider()
    }
    downslider(){
        this.timer = setInterval(() => {
            this.setState({
                top:this.state.top+2
            })
            if(this.state.top==0){
                clearInterval(this.timer)
            }
        }, 1);
        
    }
    upslider(){
        this.timer = setInterval(() => {
            this.setState({
                top:this.state.top-2
            })
            if(this.state.top<=-200){
                clearInterval(this.timer);
                this.props.setinputAlert(false)
            }
        }, 1);
    }
    render(){
        var height= this.props.height;
        return  <div className="inputAlert-container" id="inputAlert-container" 
                    style={{width:`${height*1.775+10}px`,height:`${height*0.6}px`,top:this.state.top+"px"}}
                    onClick={()=>{
                        this.upslider()
                    }}
                >
                    <div className="inputAlert-child-container" >
                        <input autoFocus="autofocus" type="text" id ='inputAlert'
                            autoComplete="off"
                            onChange={(e)=>{
                                this.props.setPropsMoney(e.target.value)
                            }}
                            maxLength={`${this.props.maxLength}`}
                            value={this.props.money}
                        />  
                    </div>
                </div>
    }
}