import React from "react";
import WithDrawCash from "../../container/WithDrawCash";
import './Present.less';
const api="http://111.231.57.144:8081/api";
export default class Present_withdrawCash extends React.Component{
    constructor(props){
        super(props);
        this.state={ 
            money:'',
            present:''
        }
    }  
    setPath(val){
        this.props.setProps(val)
    }
     //避免异步请求结果未返回就切换页面报错
     componentWillUnmount(){
        this.setState = (state,callback)=>{
          return;
        };
    }
    render(){
         //定义变量，固定弹窗的高度
         const height =this.props.height;
        const {user_id,nick_name, client,proxy_id,proxy_name,from_brand,gold} = this.props.data;
        return <WithDrawCash current="2" setPath={this.setPath.bind(this)} >
                 <div className="ZfbBox1">
                 <div  className="ZfbBox1_child2" style={{top:'20%'}} >
                            <p className="WordArt">赠送ID:</p>
                            <div  className="publicinput">
                                <input type="text" placeholder="输入ID"
                                    autoComplete="off"
                                    maxLength="10"
                                    onChange={(e)=>{
                                        this.setState({
                                            present:e.target.value
                                        })
                                    }}
                                    onInput={(e)=>{
                                        e.target.value= e.target.value.match(/\d+(\.\d{0,2})?/) ? e.target.value.match(/\d+(\.\d{0,2})?/)[0] : ''
                                    }} 
                                    
                                    value={this.state.present}
                                />
                                {/* 点击清空金额 */}
                                <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)"}}
                                    onClick={()=>{
                                        this.setState({
                                            present:''
                                        })
                                    }}
                                        
                                ></i>
                            </div>
                        </div>
                        <div  className="ZfbBox1_child2" >
                            <p className="WordArt">赠送金额:</p>
                            <div  className="publicinput">
                                <input id="userInput" type="text" placeholder="请输入赠送金额"
                                    autoComplete="off"
                                    maxLength="7"
                                    onChange={(e)=>{
                                        this.setState({
                                            money:Number(e.target.value)
                                        })
                                    }}
                                    onInput={(e)=>{
                                        e.target.value= e.target.value.match(/\d+(\.\d{0,2})?/) ? e.target.value.match(/\d+(\.\d{0,2})?/)[0] : ''
                                    }} 
                                    value={this.state.money}
                                />
                                {/* 点击清空金额 */}
                                <i style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/75.png)"}}
                                    onClick={()=>{
                                        this.setState({
                                            money:''
                                        })
                                    }}
                                        
                                ></i>
                            </div>
                        </div>
                    </div>
                    <div className="ZfbBox2"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/12.png)",padding:"2%",color:'rgba(111, 73, 52, 255)'}}>
                        注意：申请完赠送后，请到赠送历史中确认是否立即赠送，若半小时内未确认，系统将默认发送
                    </div>
                    <div className="DhBox3">
                        <p 
                            onClick={()=>{
                                this.props.setProps("#/withdrawCash/presenthistory")
                            }}
                        >赠送记录</p>
                        <div style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}}
                            className="publicbtn"
                            onClick={()=>{
                                // eslint-disable-next-line 
                                if(this.state.money!=""&&this.state.money<=gold&&this.state.money>=50){
                                    this.setState({
                                        msg:"",
                                        isAlert2:true,
                                    })
                                }else if(this.state.money>gold){
                                    this.setState({
                                        msg:'赠送金额不能大于余额！',
                                        isAlert:true
                                    })
                                }else if(this.state.money<=50){
                                    this.setState({
                                        msg:'最小赠送金额需大于等于50！',
                                        isAlert:true
                                    })
                                }else{
                                    this.setState({
                                        msg:"金额不能为空！",
                                        isAlert:true
                                    })
                                }
                                
                            }}
                        >申请赠送</div>
                    </div> 
                     {/* 上分请求弹窗 */}
             {
                    !this.state.isAlert2?null:<div className="MyorderAlert-container" id="MyorderAlert-container"   
                    // style={{width:`${height*1.775}px`,height:`${height}px`,left:"50%",marginLeft:-(height*1.775/2)}}
                    style={{width:`${height*1.775+10}px`,height:`${height}px`,position:"fixed",left:0,top:0}}
                    >
                    <div className="MyorderAlert-child-container"  style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/alertbg.jpg)"}} >
                        <div className="MyorderAlert-child" >
                        {/*  eslint-disable-next-line  */}
                            {this.state.msg}     
                        </div>
                        <div className="MyorderAlert-btn" >
                                <div 
                                    className='publicbtn'
                                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                                    onClick={()=>{
                                    this.setState({
                                        isAlert2:false
                                    })
                                }}>否</div>
                                <div
                                    className='publicbtn'
                                    style={{backgroundImage:"url("+process.env.PUBLIC_URL+"/images/dd8.png)"}} 
                                    onClick={()=>{
                                        //发送上分请求
                                        fetch(`${api}/replace_payment/addScore?user_id=${this.state.shangfenId}&replace_id=${user_id}&amount=${this.state.input_money}&order_id=${this.state.order_id}`,{
                                            method:"GET"
                                        }).then((data)=>data.json())
                                        .then((responseJson)=>{
                                            // eslint-disable-next-line
                                            if(responseJson.status==0){
                                                this.setState({
                                                    upScorestatus:responseJson,
                                                    isAlert:true,
                                                    msg:"上分成功!"
                                                })
                                            }else{
                                                this.setState({
                                                    upScorestatus:responseJson,
                                                    isAlert:true,
                                                    msg:responseJson.msg
                                                })
                                            }
                                            this.hideAlert();
                                            this.setState({
                                                isAlert2:false
                                            })
                                        })
                                        .catch((error) =>{
                                            alert(error)
                                        })
                                    }}
                                >
                                    是
                                </div>
                            </div>
                        </div>
                    </div>
                }
            {/* 提示信息弹窗 */}
            {
            !this.state.isAlert
                ?null
                :<div className="Alert-container" id="Alert-container"   style={{width:`${height*1.775+10}px`,height:`${height}px`,position:"fixed",left:0,top:0}}>
                <div className="Alert-child-container" >
                    <div className="Alert-child">
                    {/* eslint-disable-next-line  */}
                        { this.state.msg}     
                    </div>
                    <div className="Alert-btn">
                            <div
                                onClick={()=>{
                                    fetch(`${api}/with_draw/index?user_id=${user_id}&withdraw_type=${this.state.withdraw_type}`,{
                                        method:"GET"
                                    }).then((data)=>data.json())
                                    .then((responseJson)=>{
                                       this.setState({
                                            indexResults:responseJson
                                       })
                                    })
                                    .catch((error) => {
                                        alert(error)
                                    })
                                    this.setState({
                                        isAlert:false
                                    })
                                }}
                            >
                                确定
                            </div>
                        </div>
                    </div>
                </div>
            }
        </WithDrawCash>
    } 
}
